@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{box-sizing:border-box}html,body,#root{width:100%;height:100%;font-size:16px}.app-container{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.app-container .main-container{min-width:600px;height:70%;display:flex;border:1px solid #999;border-radius:3px;flex-direction:column}.app-container .main-container .top-section{flex:0 0 50%}.app-container .main-container .bottom-section{flex:0 0 50%}.btn{border:1px solid #ddd;margin:15px 0;cursor:pointer}.btn:focus{outline:nonel}.popup-container{position:absolute;will-change:transform;transform:translate3d(487px, 177px, 0px);top:10px;left:0px;transform:translate(563px, 274px);width:15em;height:6em;display:flex;border-radius:4px;background-color:#fff;box-shadow:0px 0px 5px 2px rgba(15,15,15,0.2);font-size:1rem;padding:0.5em}.form-container label{font-size:0.8rem}

.top-container{display:flex;flex-direction:column;font-family:'Oxygen', sans-serif;align-items:center;background-color:#445a71;height:100%;width:100%}.top-container .title{color:#fff;font-size:1rem;margin-top:0.4em}.top-container .weather-container{color:#fff;margin-top:1em}.top-container .weather-container-header{display:flex;flex-direction:row;justify-content:center;font-size:1.8rem}.top-container .weather-container-inner{display:flex;flex-direction:row;justify-content:center;margin-top:0.8em;align-items:center}.top-container .weather-container-inner .weather-container-temperature{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;font-size:2rem;margin-top:0.8em}.top-container .weather-container-inner .weather-container-image{width:7em;height:5em}.top-container .weather-container-inner .weather-container-image img{width:100%;height:120%;object-fit:contain;object-position:-50% 50%}.top-container .weather-container-footer{display:flex;flex-direction:row;justify-content:center;font-size:1.4rem;margin-bottom:0.5em}.btn-select-location{background-color:#49DCC2;font-size:0.7rem;font-family:'Roboto', sans-serif;border-radius:2px;color:#222}

.bottom-container{width:100%;height:100%;background:linear-gradient(100deg, #6ABECA 0%, rgba(58,98,104,0.27) 100%),url(/static/media/sunny-weather.47590d77.jpg) center no-repeat;background-size:cover}.bottom-container .bottom-container-header{width:100%;padding-top:0.5em;display:flex;justify-content:center;color:#eee}.bottom-container .bottom-inner-container{position:relative;display:flex;flex-direction:row;justify-content:center;align-items:center}.forecastday-container{display:flex;flex-direction:column;width:100%;height:100%;align-items:center;justify-content:center;border:#C0C0C0 solid 1px;margin:0.8em;border-radius:5px;color:#eee;background:rgba(46,113,236,0.7)}.forecastday-container .forecastday-container-image{width:5em;height:5em}.forecastday-container .forecastday-container-image img{width:100%;height:100%}.forecastday-container .forecastday-container-text{font-size:0.8rem}.tooltip{position:absolute;display:inline-block}.tooltip .tooltiptext{visibility:hidden;width:18%;background-color:#555;color:#fff;text-align:center;padding:5px 0;border-radius:6px;font-size:0.8rem;position:absolute;z-index:1;margin-left:0px;opacity:0;transition:opacity 0.3s}.tooltip .tooltiptext::after{content:"";position:absolute;margin-left:-5px;border-width:5px;border-style:solid;border-color:#555 transparent transparent transparent}.tooltip:hover .tooltiptext{visibility:visible;opacity:1}

